'use strict';


/* Application routes */
mimicTrade.config(['$stateProvider', '$urlRouterProvider', '$locationProvider', 
	function($stateProvider, $urlRouterProvider, $locationProvider){
		$stateProvider
		.state('profile',{
			url: '/profile',
			controller: 'profileCtrl',
			templateUrl: '/user/profile/views/profile.html',
			authenticate: true,
			resolve: {
				deps: ['$ocLazyLoad', function($ocLazyLoad) {
					return $ocLazyLoad.load({
					    name: 'mimicTrade',
					    insertBefore: '#ng_load_plugins_before',
					    files: [
						'../assets/pages/css/profile.min.css',
					    ] 
					});
				}],
				userProfile: ['profileSrv', (profileSrv) => profileSrv.getUserInfo()]
			},
			data: {pageTitle: 'My Profile'}
		});
	}
]);
