/* global FileReader */
'use strict';
mimicTrade.controller('profileCtrl', ['$scope', '$state', 'RestSvr', '$rootScope', 'Upload','profileSrv','$timeout','alertService','userProfile',
	($scope, $state, RestSvr, $rootScope, Upload, profileSrv, $timeout, alertService, userProfile) => { 
		// Intialize default variables for cropper plugin
		$scope.myImage='';
    	$scope.myCroppedImage='';
   
		$scope.newImageSelected = false;
		/**
		 * Fetch user info by loggedUser session id,
		 * profileSrv.getUserInfo()
		 *
		 * @returns {Object}
		 */
		$scope.profile = userProfile.records;
		$scope.profile.profile_update_img = $scope.profile.profile_img;

		/**
		 * Save the user profile if form is valid
		 * @params {Boolean} [isValid to check if form is valid or not]
		 *
		 * @returns {}
		 */
		$scope.saveUserProfile = (isValid) => {
			if(!isValid){
				return;
			}
			$scope.isLoading = true;
            profileSrv.saveUserInfo($scope.profile.id, $scope.profile)
            .then(response => {
            	scrollToTop();
            	
            	alertService.add('success', 'Your profile update successfully!');
            })
            .catch(err => alertService.add('danger',err.data))
            .then(() => $scope.isLoading = false);
		};

		/**
		 * User image will update based on new image
		 * if user select new image
		 * @param  {Boolean} isValid [Form is valid]
		 * @param  {[type]}  form    []
		 * @return {[type]}          [Object]
		 */
		$scope.changeAvatar = function (isValid, form) {
			if( !isValid ){
				return;
			}
			
			if(!_.isEmpty(form.profile_update_img.$error)){
				return;
			}
			if(Upload.isFile($scope.profile.profile_update_img)){
				$scope.isLoading = true;
				Upload.upload({
					url: baseUrl(`user/profile/pic/update/${$scope.profile.id}/`),
					method: 'POST',
					data: {
						profile_img: Upload.dataUrltoBlob($scope.myCroppedImage, $scope.profile.profile_update_img.name)
					}
				})
				.then( response => {
					if( response.data.result ){
						$scope.profile.profile_img = response.data.result.profile_img;
						$scope.newImageSelected = false;	
					}
					profileSrv.updateUserState($scope.profile);
					alertService.add('success', 'Your profile image update successfully!'); 
				})
				.catch(err => alertService.add('danger',err.data))
				.finally(() => $scope.isLoading = false);
			}			
		};

		/**
		 * Check if user has selected his new avatar image
		 * @return {Boolean}
		 */
		$scope.isImageSelected = function ($file, $event) {
			$scope.newImageSelected = true;	
			if( $file ) {
				let reader = new FileReader();
				reader.onload = function (evt) {
				    $scope.$apply(function($scope){
				    	$scope.myImage=evt.target.result;
				     });
				};
				reader.readAsDataURL($file);	
			}
		};

	    
		$scope.updatePassword = function (isValid, form) {
			if(!isValid){
				return;
			}
			$scope.isLoading = true;
			profileSrv.updateUserPassword($scope.profile.id, $scope.account)
			.then(response => {
				// Reset Form fields and validations
				form.$setPristine();
				form.$setUntouched();
				$timeout( () => $scope.account = {});
				alertService.add('success', 'Your password has update successfully!');
			})
			.catch(err => {
				form.password.$setValidity('wrong', false);
			})
			.then(() => $scope.isLoading = false);
		};

		/**
		 * this will clear the
		 * @param  input name attar
		 */
		$scope.clear = function (form) {
			form.password.$setValidity('wrong', true);
		};
		
		/**
		 * Get User account settings data
		 * @param  {int} selectedTab [tab id that user has selected]
		 * @return {}          
		 */
		$scope.getTabData = function (selectedTab) {
			if(selectedTab === 3)	{
				RestSvr.get(`settings/${$scope.profile.id}/`)
				.then(response => $scope.settings = response.records)
				.catch(err => alertService.add('danger', err.message));
			}
		};


		/**
		 * Update user account settings
		 * @return {Object} 
		 */
		$scope.updateSettings =  () => {
			$scope.isLoading = true;
			RestSvr.post(`settings/${$scope.settings.id}/`, $scope.settings)
				.then(response => alertService.add('success', 'Account settings has been update successfully!'))
				.catch(err => alertService.add('danger', err.message))
				.then(() => $scope.isLoading = false);
		};
	}
]);
