'use strict';
mimicTrade.controller('customerViewStrategyCtrl', ['$scope', '$state','alertService', 'customerSrv', 'strategy','traderSrv','$location',
	($scope, $state, alertService, customerSrv, strategy, traderSrv, $location) => {
		$scope.position = false;
		let queryParams = $location.search();
		if( queryParams ) {
			if( queryParams.action === 'position' ) {
				$scope.position = true;
			}
		}
		
		/**
		 * Get strategy detail by startegy id
		 * @type {Object}
		 */
		$scope.strategy = strategy.records;

		$scope.comments = [];
		$scope.commentLoader = false;
		/**
		 * This function will executes when view loaded and 
		 * then strategy comments will loaded
		 */
		let load = (page, scroll) => 
			traderSrv.getStrategyComments($scope.strategy.id, page)
			.then(response => { 
				/* global scrollToBottom */
				$scope.comments = $scope.comments.concat(response.records); 
				$scope.paging = response.paging; 
				$scope.commentLoader = true;
				if(scroll){
					scrollToBottom('commentList');	
				}
				
			})
			.catch(errors => alertService.add('danger', errors.message));
		

		load();

		/**
		 * Update page title according to strategy title
		 * @type {Object}
		 */
		$state.current = {
			data: { pageTitle: $scope.strategy.title }
		};

		/**
		 * Load more comments
		 * @return {Object}
		 */
		$scope.loadMore = () => {
			$scope.commentLoader = false;
			load(($scope.paging.page + 1), true);
		};

		/**
		 * Mark favorite/un-favorite strategy
		 * @param  {int} id [strategy id]
		 * @return {Object} 
		 */
		$scope.toggleFavorite = (favtype) => customerSrv.toggleFavorite($scope.strategy.id)
		.then(response => {
			$scope.strategy.favorite = favtype ? false : true;
			alertService.add('success',`Strategy marked as ${favtype ? 'unfavorite':'favorite'} successfully!`);
		})
		.catch(errors => alertService.add('danger',errors.message));

		/**
		 * Execute a strategy
		 * @return {} 
		 */
		$scope.execute = () => 
			customerSrv.executeStrategy($scope.strategy.id)
			.then(response => $state.go('customerStrategies'))
			.catch(errors => alertService.add('danger', errors.message));
		
	}
]);