'use strict';

mimicTrade.factory('signUpSrv', ['localStorageService', '$rootScope', '$state', 'RestSvr', 'errorHandlerSrv',
    function (localStorageService, $rootScope, $state, RestSvr, errorHandlerSrv) {
    return {
    	saveUserState: function (user) {
            var storedUser = localStorageService.get('user');
            if( storedUser ) {
                _.assign(storedUser, user);
            }
            localStorageService.set('user', storedUser);
        },
        getInfoByKey: function (key) {
            if( key ){
                return localStorageService.get(key);
            }
            return;
        },
        splitName: function (fullName) {
            if( !fullName ){
                return;
            }  
            return {
                lastName: fullName.split(' ').slice(0, -1).join(' '),
                firstName: fullName.split(' ').slice(-1).join(' ')
            };
        },
        byName: function (firstName, lastName) {
            return (firstName + ' ' + lastName);
        },
    	getInfoByUserId: function (user) {
            if($state.params){
                var user_info = localStorageService.get('user');
                if( user_info ){
                    
                    return RestSvr.post('user/details/', {id: user_info.id}).then(function (response) {
                        if( response.errors ){
                            errorHandlerSrv.error(response.errors.message);
                            return;
                        } else {
                            
                            localStorageService.set('user', response.records);
                            localStorageService.remove('user_id');
                            return response.records;
                        }
                    });
                }
            }
    	}
    };
}]);