'use strict';

mimicTrade.directive('progressButton', [ () => {
	return {
		restrict: 'E',
		scope: {
			className: '@', //attribute
			buttonText: '@',
			loading: '=', // string
			buttonType: '@?' // string , ? makes it optional
		},
		template: `<md-button ng-disabled="loading" type="{{buttonType}}" class="{{className}}" aria-label="{{buttonText}}">
						<span ng-if="!loading">{{buttonText}}</span>
						<div ng-if="loading" layout="row" layout-sm="column" layout-align="space-around">
							<md-progress-circular class="md-hue-2" md-diameter="20px" md-mode="indeterminate"></md-progress-circular>
						</div>
					</md-button>`,
		link: (scope) => {
			if( _.isEmpty(scope.buttonType) ){
				scope.buttonType = 'submit';
			}
		}
	};
}]);