'use strict';
mimicTrade.controller('closeStrategyCtrl', ['$scope', '$state','alertService', 'customerSrv', 'strategy','$location',
	($scope, $state, alertService, customerSrv, strategy, $location) => {

		/**
		 * defautl value for position
		 * @type {Boolean}
		 */
		$scope.position = false;

		/**
		 * queryParams let system know about user actions,
		 * from where user navigates so that if user click camcel,
		 * system can redirect back user to same page
		 * @type {Object}
		 */
		let queryParams = $location.search();
		if( queryParams ) {
			if( queryParams.action === 'position' ) {
				$scope.position = true;
			}
		}
		/**
		 * strategy by id
		 * @type {Object}
		 */
		$scope.strategy = strategy.records;

		/**
		 * Execute a strategy
		 * @return {} 
		 */
		$scope.execute = () => 
			customerSrv.executeStrategy($scope.strategy.id)
			.then(response => $state.go('customerStrategies'))
			.catch(errors => alertService.add('danger', errors.message));
		
		/**
		 * close function will close a customer positions
		 * @return {Object}
		 */
		$scope.close = () => customerSrv.closeStrategy($scope.strategy.id)
		.then(response => {
			alertService.add('success','Strategy has been closed successfully!');
			$state.go('customer');
		})
		.catch(errors => alertService.add('danger',errors.message));

		/**
		 * backlink will check from where user redirect ie referer
		 * then redirect next to user accordingly
		 */
		$scope.backlink = () => {
			if( $scope.position ) {
				$state.go('customer');
			} else {
				$state.go('customerStrategies');
			}
		};
	}
]);