'use strict';

mimicTrade.factory('profileSrv', ['RestSvr', 'authSrv','localStorageService', '$rootScope', 
	(RestSvr, authSrv, localStorageService, $rootScope) => {
		return {
			getUserInfo: () => RestSvr.post('user/profile/', {id: authSrv.User().id}),
			saveUserInfo: (id, data) => RestSvr.post(`user/profile/update/${id}/`, data),
			updateUserPassword: (id, data) => RestSvr.post(`user/change/password/${id}/`, data),
            updateUserState: (user) => {
                localStorageService.set('authUser', user);
                $rootScope.user = localStorageService.get('authUser');
            }
		};
	}
]);

