'use strict';
mimicTrade.controller('reviewStrategyCtrl', ['$scope', '$state','alertService', 'traderSrv', '$timeout',
	($scope, $state, alertService, traderSrv, $timeout) => {
		
		let strategyData = traderSrv.getLocallySavedStrategy();
		
		if( !strategyData ){
			alertService.add('danger', 'No local storage data found!!');
			return;
		}

		$scope.strategy = strategyData;

		/**
		 * Save strategy to DB and validate from CQG
		 * @return {Object}
		 */
		$scope.postStrategy = () => {
			$scope.isLoading = true;
			alertService.add('success', 'Please wait!! processing ...');
			traderSrv.saveStrategy($scope.strategy).then( response => {
				
				alertService.add('success', 'strategy saved successfully, once its approved by admin we will notify you!');
				$timeout(() => {
					traderSrv.emptyStrategyStorage();
					$state.go('user.strategies');
				});
			})
			.catch(error => alertService.add('danger', error.message))
			.then(() => $scope.isLoading = false);
		};

		$scope.returnBack = () => $state.go('newStrategy');
	}
]);	