'use strict';
mimicTrade.factory('customerSrv',['$rootScope', 'RestSvr', 'localStorageService','authSrv','$q','dialogService',
	($rootScope, RestSvr, localStorageService, authSrv, $q, dialogService) => {
		return {
			getAllTradersStrategies: (type, page = 1) => RestSvr.paginate('strategy/customer/list/', {type, page, customer_id: authSrv.User().id}),
			toggleFavorite: (strategy_id) => RestSvr.paginate('strategy/favorite/', {strategy_id, customer_id: authSrv.User().id}),
			getStrategyById: (strategy_id) => RestSvr.post(`strategy/view/customer/${strategy_id}/`, {customer_id: authSrv.User().id}), // get a detail of single strategy by id
			executeStrategy: (strategy_id) => {
				return $q((resolve, reject) => 
					RestSvr.post('strategy/trade/customer/', {strategy_id, customer_id: authSrv.User().id})
					.then(response => 
						resolve(
							dialogService.show({
								content: '<strong>CONFIRMED</strong><p>Your trades have been executed as per the price(s) listed.</p>',
								buttonText: 'BACK TO ALL POSITIONS',
								outsideClose: false
							})
						)	
					)
					.catch(errors => reject(errors))
				);
			},
			closeStrategy: (strategy_id) => RestSvr.post('strategy/close/customer/', {strategy_id, customer_id: authSrv.User().id})
		};
	}
]);
