'use strict';
mimicTrade.controller('loginCtrl', ['$scope', '$state', 'RestSvr', '$rootScope', '$log','$timeout','loginSrv','alertService',
	($scope, $state, RestSvr, $rootScope, $log, $timeout, loginSrv, alertService) => {
		
		/**
		 * login function will authenticate user 
		 * and redirect to specific dashboard
		 * @param  {Boolean} isValid [Check if form is valid]
		 * @return {none}   [User will redirect to dashboard]
		 */
		$scope.login = (isValid) => {
			if( !isValid ){
				return;
			}
			$scope.isLoading = true;
			RestSvr.login('login/', $scope.user)
			.then( response => {
				
				/**
				 * loginSrv will initialize user session
				 * and store user data into localStorage
				 * $rootScope.user assign a global variable
				 */
				
				loginSrv.initUserSession(response.user, response.token);
				if( response.user.user_type === "Trader" ){
					$state.go('strategies');		
				} else {
					$state.go('customer');		
				}
				
			})
			.catch(errors => alertService.add('danger',errors.message,true))
			.then(() => $scope.isLoading = false);
		};

		/**
		 * function will check the user email
		 * and send the password reset link to url
		 * @param  {Boolean} isValid [check if form is valid]
		 * @return {}
		 */
		$scope.forgot = (isValid, form) => {
			if( !isValid ){
				return;
			}
			
			$scope.isLoading = true;
			RestSvr.post('forgotpassword/', {email: $scope.user.email})
			.then(response => {
				$scope.isLoading = false;
				alertService.add('success', 'Kindly check your inbox/spam folder for further instructions!!');

				// Reset Form fields and validations
				form.$setPristine();
				form.$setUntouched();
				$timeout( () => $scope.user = {});
			})
			.catch(errors => alertService.add('danger', errors.message))
			.then(() => $scope.isLoading = false);
		};
	}
]);