'use strict';
mimicTrade.controller('positionCtrl', ['$scope', '$state', 'RestSvr', '$rootScope', '$location', 'positions','positionSrv','alertService','$timeout',
	function($scope, $state, RestSvr, $rootScope, $location, positions, positionSrv, alertService, $timeout){
		$scope.positions = positions.records;
		$scope.paging = positions.paging;

		/**
		 * Strategies filters list
		 * @type {Array}
		 */
		$scope.filters = ['All position','Grains','Energies','Indicies','Forex','Other','Closed Positions'];
		$scope.whenSelected = $scope.whenSelected || 'All position'; // default selected filter

		/**
		 * Fetch all the position using position service
		 * @param  {Int} page   [page number]
		 * @param  {string} status [strategy status]
		 * @return {Object}
		 */
		let getAllPositions = (page, status) => {
			$scope.positions = false;
			positionSrv.getAllPositions(page, status)
			.then(({paging, records}) => {
				$timeout(() => {
					$scope.positions = records;
					$scope.paging = paging;
				},500);
			})
			.catch(error => alertService.add('danger', error.message));
		};

		/**
		 * Filter function for strategies filter
		 * @param  {string} _filter [clicked item name]
		 * @return {}
		 */
		$scope.filter = (_filter) => {
			$scope.whenSelected = _filter;
			getAllPositions($scope.paging.page, _filter);
		};	

		/**
		 * pageChanged execute whenever ui pagination page number clicked
		 * @return {Object}
		 */
		$scope.pageChanged = () => getAllPositions($scope.paging.page);
	}
]);