'use strict';

/* Application routes */
mimicTrade.config(['$stateProvider',($stateProvider) => {

	$stateProvider
	.state('notifications',{
		url: '/notifications',
		controller: 'notificationCtrl',
		templateUrl: '/user/notifications/views/notifications.html',
		data: {pageTitle: 'Notifications'},
		resolve: {
		    deps: ['$ocLazyLoad', ($ocLazyLoad) => {
		        return $ocLazyLoad.load({
		            name: 'mimicTrading',
		            insertBefore: '#ng_load_plugins_before', // load the above css files before a LINK element with this ID. Dynamic CSS files must be loaded between core and theme css files
		            files: [
		                '../assets/apps/css/inbox.min.css', 
		            ] 
		        });
		    }],
		    notifications: ['notificationSrv', (notificationSrv) => notificationSrv.getNotificationList()]
		},
		authenticate: true
	});
}]);