'use strict';
mimicTrade.controller('traderCtrl', ['$scope','traderSrv', 'alertService', 'strategiesList', '$timeout','$log','$state',
	($scope, traderSrv, alertService, strategiesList, $timeout, $log, $state) => {
		/**
		 * Strategies filters list
		 * @type {Array}
		 */
		$scope.filters = ['all','pending','available','trading','archived'];
		$scope.whenSelected = $scope.whenSelected || 'all'; // default selected filter

		/**
		 * Strategies data from route resolve
		 * @type {Object}
		 */
		$scope.strategies = strategiesList.records;
		$scope.paging = strategiesList.paging;

		// default settings for pagination
		$scope.maxSize = 10;
		$scope.forceEllipses=true;

		/**
		 * Filter function for strategies filter
		 * @param  {string} _filter [clicked item name]
		 * @return {}
		 */
		$scope.filter = (_filter) => {
			$scope.strategies = false;
			$scope.whenSelected = _filter;
			getMyStrategies($scope.paging.page, _filter);
		};	
		
		/**
		 * Whenever pagination changed this method will called automatically
		 * @return {int} [clicked pageNumber]
		 */
		$scope.pageChanged = () => getMyStrategies($scope.paging.page, $scope.whenSelected);
		

		/**
		 * Helper function which will be used by pagination 
		 * and filter function
		 * @param  {int} 	page   [page number that clicked]
		 * @param  {string} status [strategy status]
		 * @return {object}
		 */
		let getMyStrategies = (page, status) => 
			traderSrv.getMyStrategies(page, status)
			.then(({records, paging}) => { 
				// Just to show loader for bit of time
				$timeout(() => {$scope.strategies = records; $scope.paging = paging; }, 500);
			})
			.catch(error => alertService.add('danger', error.message));

		/**
		 * This function will convert sting to JSON Object
		 * @param  {String}
		 * @return {Object}
		 */
		$scope.convertToJSON = (data) => {
			
			if( data ){
				if( typeof(data) === 'string' ){
					return JSON.parse(data);
				}
			}
			return [];
		};
		

		/**
		 * Remove strategy will archived the strategy in Db
		 * @param  {Object} event [click event handler]
		 * @return {Object}
		 */
		$scope.removeStrategy = (event, id, status) => {
			if(status === 'Archived'){
				scrollToTop();
				alertService.add('success', 'Strategy has already archived!');
				return;
			}
			
			traderSrv.archivedStrategy(event, id)
			.then(response => {
				$state.reload();
				alertService.add('success', 'Strategy has been archived successfully');
			})
			.catch(errors => $log.info(errors));
		};
	}
]);	