'use strict';
mimicTrade.controller('customerCtrl', ['$scope', '$state','alertService', 'customerSrv', '$timeout','strategies','authSrv',
	($scope, $state, alertService, customerSrv, $timeout, strategies, authSrv) => {
		/**
		 * Strategies data from route resolve
		 * @type {Object}
		 */
		$scope.paging = {page : 1};
		$scope.strategies = strategies.records;
		$scope.paging = strategies.paging;
		/**
		 * Strategies filters list
		 * @type {Array}
		 */
		$scope.filters = ['All Markets','Grains','Energies','Indicies','Forex','Other'];
		$scope.whenSelected = $scope.whenSelected || 'All Markets'; // default selected filter

		/**
		 * Filter function for strategies filter
		 * @param  {string} _filter [clicked item name]
		 * @return {}
		 */
		$scope.filter = (_filter) => {
			$scope.whenSelected = _filter;
			getAllTradersStrategies(_filter, $scope.paging.page);
		};	

		/**
		 * Helper function which will be used by pagination 
		 * and filter function
		 * @param  {int} 	page   [page number that clicked]
		 * @param  {string} status [strategy status]
		 * @return {object}
		 */
		let getAllTradersStrategies = (status, page) => {
			$scope.strategies = false;
			customerSrv.getAllTradersStrategies(status, page)
			.then(({records, paging}) => { 
				// Just to show loader for bit of time
				$timeout(() => {$scope.strategies = records; $scope.paging = paging; }, 500);
			})
			.catch(error => alertService.add('danger', error.message));
		};	
		

		/**
		 * Whenever pagination changed this method will called automatically
		 * @return {int} [clicked pageNumber]
		 */
		$scope.pageChanged = () => getAllTradersStrategies($scope.whenSelected, $scope.paging.page);

		/**
		 * Get all the favorites listings
		 * @return {Object}
		 */
		$scope.getMyFavorites = (fav) => {
			$scope.whenSelected = fav;
			getAllTradersStrategies($scope.whenSelected, $scope.paging.page);
		};

		/**
		 * Mark favorite/un-favorite strategy
		 * @param  {int} id [strategy id]
		 * @return {Object} 
		 */
		$scope.toggleFavorite = (id, favorite, index) => customerSrv.toggleFavorite(id)
		.then(response => {
			$scope.strategies[index].favorite = favorite ? false: true;
			
			if( $scope.whenSelected === 'fav' ){
				$scope.strategies.splice(index, 1);
			}
			alertService.add('success',`Strategy marked as ${favorite ? 'unfavorite':'favorite'} successfully!`);
		})
		.catch(errors => alertService.add('danger',errors.message));
		
	}
]);	