'use strict';
mimicTrade.factory('alertService',['$rootScope', '$timeout', function($rootScope, $timeout) {
	var alertService = {};

    alertService.add = function(type, msg, pop) {
        $rootScope.alert = {'type': type, 'msg': msg};
    };

    alertService.closeAlert = function(index) {
        $rootScope.alert = null;
    };

    return alertService;
}]);