'use strict';
mimicTrade.factory('positionSrv',['$rootScope', 'RestSvr', 'localStorageService','authSrv','dialogService','$q',
	($rootScope, RestSvr, localStorageService, authSrv, dialogService, $q) => {
		return {
			getAllPositions: (page = 1, status=null) => RestSvr.paginate('strategy/trade/customer/list/', {customer_id: authSrv.User().id, page, status})
		};
	}
]);