'use strict';
mimicTrade.controller('strategiesBkpCtrl', ['$scope', '$state', 'RestSvr', '$rootScope', '$location','$mdDialog', 'localStorageService', 'authSrv','alertService',
	function($scope, $state, RestSvr, $rootScope, $location, $mdDialog, localStorageService, authSrv, alertService){

		/**
		 * Current date for every new strategy
		 * @type {Date Object}
		 */
		$scope.today = new Date();

		/**
		 * Array of months between start date to end date
		 * @type {Array}
		 */
		$scope.monthsArr = getDates($scope.today, moment($scope.today).add(1, 'months'));

		/**
		 * Strike price range between two constant of prices
		 * @type {Array}
		 */
		$scope.strikePriceRange = _.range(1100, 2020, 20);

		
		/**
		 * Strategy Object which will hold the info of strategy	
		 * @type {Object}
		 */
		$scope.strategy = {};

		
		/**
		 * Number of legs that contained by a Strategy
		 * @type {Array}
		 */
		$scope.strategy.legs = [];

		
		/**
		 * Number of legs straddle has
		 * @type {Array}
		 */
		$scope.straddle_legs = [1,2,4,5];
		
		/**
		 * check if form is valid only then submit the form
		 * @param  {Boolean} isValid [description]
		 * @return {[type]}          [description]
		 */
		
		$scope.createNewStrategy = function (isValid) {
			if( !isValid ){
				return;
			}
			var user = authSrv.User();
			_.assign($scope.strategy, {user_id: user.id});
			RestSvr.post('strategies/create/', $scope.strategy).then(function (response) {
				if( response.error ){
					alertService.add('danger', response.error.message);
				} else {
					$state.go('user.strategies');
				}
			});
		};


		/**
		 * return an array of dates between start abd end date
		 * @param  {Date} startDate 
		 * @param  {Date} stopDate
		 * @return {Array}           [array that contains dates]
		 */
		function getDates(startDate, stopDate) {
		    var dateArray = [];
		    var currentDate = moment(startDate);
		    stopDate = moment(stopDate);
		    while (currentDate <= stopDate) {
		        dateArray.push( moment(currentDate).format('MMM DD') );
		        currentDate = moment(currentDate).add(1, 'days');
		    }
		    return dateArray;
		}
	}
]);