'use strict';

/* Application routes */
mimicTrade.config(['$stateProvider', '$urlRouterProvider',function($stateProvider, $urlRouterProvider){
	$stateProvider
	.state('signup',{
		url: '/signup',
		controller: 'signupCtrl',
		templateUrl: '/user/signup/views/signup.html',
		authenticate: false,
		data: {pageTitle: 'Signup'}
	})
	.state('signup.agreement',{
		url: '/agreement',
		controller: 'signupCtrl',
		templateUrl: '/user/signup/views/agreement.html',
		authenticate: false,
		data: {pageTitle: 'Agreement and Disclaimer'}
	})
	.state('signup.two',{
		url: '/step-2',
		controller: 'signupCtrl',
		templateUrl: '/user/signup/views/step_2.html',
		authenticate: false,
		data: {pageTitle: 'Signup'}
	})
	.state('signup.three',{
		url: '/step-3',
		controller: 'signupCtrl',
		templateUrl: '/user/signup/views/step_3.html',
		authenticate: false,
		data: {pageTitle: 'Signup customer on-boarding'}
	})
	.state('signup.four',{
		url: '/step-4',
		controller: 'signupCtrl',
		templateUrl: '/user/signup/views/step_4.html',
		authenticate: false,
		data: {pageTitle: 'Signup customer on-boarding'}
	})
	.state('signup.five',{
		url: '/step-5',
		controller: 'signupCtrl',
		templateUrl: '/user/signup/views/step_5.html',
		authenticate: false,
		data: {pageTitle: 'Signup customer on-boarding'}
	})
	.state('signup.six',{
		url: '/step-6',
		controller: 'signupCtrl',
		templateUrl: '/user/signup/views/step_6.html',
		authenticate: false,
		data: {pageTitle: 'Signup customer on-boarding'},
		resolve: {
		    deps: ['$ocLazyLoad', function($ocLazyLoad) {
		        return $ocLazyLoad.load({
		            name: 'mimicTrade',
		            insertBefore: '#ng_load_plugins_before',
		            files: [
		                '/CQGWebAPIJavaScriptClient/scripts/realTime.js',
		                '/CQGWebAPIJavaScriptClient/scripts/account.js',
		                '/CQGWebAPIJavaScriptClient/scripts/auxiliary.js',
		                '/CQGWebAPIJavaScriptClient/scripts/sessionInfo.js',
		                '/CQGWebAPIJavaScriptClient/scripts/timeAndSales.js',
		                '/CQGWebAPIJavaScriptClient/scripts/timeBars.js',
		                '/CQGWebAPIJavaScriptClient/scripts/webapi.js',
		                '/CQGWebAPIJavaScriptClient/scripts/ui.js'
		            ] 
		        });
		    }]
		},
	})
	.state('signup.confirm',{
		url: '/confirm',
		templateUrl: '/user/signup/views/signup_confirmation.html',
		authenticate: false,
		data: {pageTitle: 'Signup Confirmation'}
	})
	.state('signup.failed',{
		url: '/failed',
		templateUrl: '/user/signup/views/signup_failed.html',
		authenticate: false,
		data: {pageTitle: 'Signup Confirmation'}
	})
	.state('thankyou',{
		url: '/thank-you',
		templateUrl: '/user/signup/views/thankyou.html',
		authenticate: false,
		data: {pageTitle: 'Thank You'}
	});
}]);	