'use strict';
mimicTrade.factory('dialogService',['$rootScope', '$timeout', '$mdDialog', ($rootScope, $timeout, $mdDialog) => {
	return {
	    show: (obj = {}) => {
	        return $mdDialog.show(
	          	$mdDialog.alert()
		            .parent(angular.element(document.querySelector( obj.container || 'body')))
		            .clickOutsideToClose(obj.outsideClose || true)
		            .htmlContent(obj.content)
		            .ariaLabel('Alert Dialog Demo')
		            .ok( obj.buttonText || 'close')
		            .targetEvent(obj.event)
	        );
	    },
	    confirm: (obj = {}) => {
	    	return $mdDialog.show(
	    		$mdDialog.confirm()
	    			.title(obj.title || 'Are you sure you want to proceed!!')
	    			.parent(angular.element(document.querySelector( obj.container || 'body')))
	    			.htmlContent(obj.content)
	    			.ariaLabel('Confirm Dialog')
    	          	.targetEvent(obj.event)
    	          	.ok(obj.okText || 'Yes')
    	          	.cancel(obj.cancelText || 'Cancel')

	    	);
	    }
	};
}]);