'use strict';
mimicTrade.controller('generatePasswordCtrl', ['$scope', '$state', 'RestSvr', '$rootScope', 'dialogService','$timeout','loginSrv','alertService','$location',
	($scope, $state, RestSvr, $rootScope, dialogService, $timeout, loginSrv, alertService, $location) => {
		let queryParams = $location.search();
		
		if( !queryParams.id ){
			$state.go('login');
			return;
		}
		
		/**
		 * User Generate Password
		 * @param  {Boolean} isValid [form is valid or not]
		 * @return {}          [Redirect user to login]
		 */
		$scope.generatePassword =  (isValid)  => {
			if(!isValid){
				return;
			}
			
			$scope.isLoading = true;
			RestSvr.post(`trader/change/password/${queryParams.id}/`, { password: $scope.user.password})
			.then(response => {
				$location.search('');
				$scope.isLoading = false;
				dialogService.show({
					content: '<div>Your Password has Generated successfully, you can login !!</div>',
					buttonText: 'Login'
				})
				.then(success =>  $state.go('login'));
			})
			.catch(errors => alertService.add('danger', errors.message))
			.then(() => $scope.isLoading = false);
		};
	}
]);		