'use strict';
mimicTrade.controller('notificationCtrl', ['$scope','$state','notifications','notificationSrv','alertService','dialogService','$rootScope',
	($scope, $state, notifications, notificationSrv, alertService, dialogService, $rootScope) => {
		let originatorEv;
		$scope.notifications = notifications.records;
		$scope.paging = notifications.paging;

		$scope.selected = [];

		/**
		 * load function will reload the list when pagination changes
		 * @param  {int} page [page number]
		 * @return {Object}
		 */
		let load = (page) => notificationSrv.getNotificationList(page)
		.then(({records, paging}) => {
			$scope.notifications = records;
			$scope.paging = paging;
		})
		.catch(errors => console.log(errors));

		/**
		 * when pagination clicked pageChanged will triggered automatically
		 * @return {}
		 */
		$scope.pageChanged = () => load($scope.paging.page);
	
		/**
		 * toggle will check and uncheck the checkbox
		 * @param  {int} item
		 * @param  {array} list
		 */
		$scope.toggle = (item, list) => {
		    let idx = list.indexOf(item);
		    if (idx > -1) {
		    	list.splice(idx, 1);
		    } else {
		      	list.push(item);
		    }
		};

		/**
		 * check if selected item already exists in list array
		 * @param  {int} 
		 * @param  {array} list
		 * @return {Boolean}
		 */
		$scope.exists = (item, list) =>  list.indexOf(item) > -1;

		/**
		 * isChecked function will let the system know about list array length is equals to notifications array length
		 * @return {Boolean}
		 */
		$scope.isChecked = () => $scope.selected.length === $scope.notifications.length;

		/**
		 * indeterminate state of a checkbox when not all checkbox selected
		 * @return {Boolean}
		 */
		$scope.isIndeterminate = () => ($scope.selected.length !== 0 && $scope.selected.length !== $scope.notifications.length);
		 
		/**
		 * toggleAll will use to toggle all the checkboxes 
		 * @return {}
		 */
		$scope.toggleAll = () => {			
			if ($scope.selected.length === $scope.notifications.length) {
		    	$scope.selected = [];
		    } else if ($scope.selected.length === 0 || $scope.selected.length > 0) {
		    	$scope.selected = $scope.notifications.map(x => x.id).slice(0);
		    }
		};

		$scope.openMenu = ($mdMenu, ev) => {
      		originatorEv = ev;
      		$mdMenu.open(ev);
    	};

    	$scope.markAsRead = (event) => {
    		
    		if($scope.selected.length > 0){
    			dialogService.confirm({
					title: 'Confirm ?',
					content: 'Are you sure you would like to procceed with multiple selection?',
					event: event
				})
				.then( () => notificationSrv.markAsReadNotifications($scope.selected))
				.then(response => $state.reload())
				.catch(() => console.log('action cancelled!'));
    		} else {
    			alertService.add('danger', 'Please select atleast one notification');
    		}
    	};

    	$scope.delete = (event) => {
    		if($scope.selected.length > 0){
    			dialogService.confirm({
					title: 'Confirm ?',
					content: 'Are you sure you would like to remove multiple notifications?',
					event: event
				})
				.then( () => notificationSrv.deleteNotifications($scope.selected))
				.then( () => $rootScope.manageCount())
				.then(response => $state.reload())
				.catch(() => console.log('action cancelled!'));
    		} else {
    			alertService.add('danger', 'Please select atleast one notification');	
    		}
    	};
	}
]);