'use strict';
mimicTrade.factory('traderSrv',['$rootScope', 'RestSvr', 'localStorageService','authSrv','dialogService','$q',
	($rootScope, RestSvr, localStorageService, authSrv, dialogService, $q) => {
		return {
			getSubSectors: (trader_id) => RestSvr.post('traders/subsector/', {trader_id: trader_id}),
			getContracts: (sub_sector_id) => RestSvr.post('traders/instrument/', {sub_sector_id: sub_sector_id}), // contracts -> instrument
			getSpreads: (contract_id) => RestSvr.post('traders/spreads/', {instrument_id: contract_id}), // instrument -> spread
			saveStrategy: (data) => {
				if( data.legs.length > 0 ){
					for (var i = data.legs.length - 1; i >= 0; i--) {
						data.legs[i].exchange_name_abbreviation = JSON.stringify(data.legs[i].exchange_name_abbreviation);
					}
				}
				// save strategy to DB
				return RestSvr.post('strategies/create/', data);
			},
			saveStrategyToLocal: (data) => localStorageService.set('review_strategy', data), // save strategy to localStorage 
			getLocallySavedStrategy: () => localStorageService.get('review_strategy'), // save strategy to localStorage 
			emptyStrategyStorage: () => localStorageService.remove('review_strategy'), // Remove the stored startegy data in localStorage
			getMyStrategies: (page = 1, status = null) => RestSvr.paginate('strategy/list/', {trader_id: authSrv.User().id}, {page: page,status: status}), // Reterieve all the strategies listing to trader account
			getStrategyById: (id) => RestSvr.post(`strategy/view/${id}/`, {trader_id: authSrv.User().id}), // get a detail of single strategy by id
			saveStrategyComments: (data, id) => RestSvr.post('strategy/comment/', {trader_id: authSrv.User().id, strategy_id: id, comment: data}),
			getStrategyComments: (id, page = 1) => RestSvr.paginate('strategy/comment/listing/', {trader_id: authSrv.User().id, strategy_id: id, page: page}),
			archivedStrategy: (event, id) => {
				return $q((resolve, reject) => {
					dialogService.confirm({
						title: 'Confirm ?',
						content: '<p>This will notify all active followers and remove the <br /> strategy from the marketplace</p>',
						okText: 'Yes, Remove This Strategy',
						event: event
					})
					.then( 
						() => resolve(RestSvr.post('strategy/remove/', {strategy_id: id})),  
						() => reject('You have cancelled the dialog!!')
					);	
				});
				
			}
		};
	}
]);
