'use strict';
mimicTrade.controller('signupCtrl', ['$scope', '$state', 'RestSvr', '$rootScope', 'signUpSrv', '$location', 'webSocketSvr', 'localStorageService', 'alertService','websocketService', '$log',
	function($scope, $state, RestSvr, $rootScope, signUpSrv, $location, webSocketSvr, localStorageService, alertService, websocketService, $log){
		let session_token;
		$scope.$on('$viewContentLoaded', function() {
			if($state.current.name == 'signup.six') {
				// initialize core components
				require(["ProtoBuf"], function (ProtoBufInit) {
				    if ('WebSocket' in window) {
				        if ('ArrayBuffer' in window) {
				            initPage();
				        } else {
				            $log.info("ArrayBuffer is not supported");
				        }
				    } else {
				        $log.info("WebSockets are not supported");
				    }
				});

			}	
		});

		$scope.user = {};
		/**
		 * We need to set state of user on step2 from the information saved in DB
		 */
		if($state.current.name == 'signup.two') {
			signUpSrv.getInfoByUserId().then(function (data) {
				$scope.user = data;
			});
		} else {
			if( signUpSrv.getInfoByKey('user') ){
				$scope.user = signUpSrv.getInfoByKey('user');
			}	
		}
		

		/* Whether we want to show the hints or not */
		$scope.showHints = true;

		/**
		 * When user agree to terms of services
		 * @param  {Boolean} isValid [description]
		 * @return {}
		 */
		$scope.agreementTerms = function (isValid) {
			if( !isValid ){
				return;
			}
			$state.go('signup.two');
		};

		/**
		 * this function will pre-validate the customer onboarding step 1 form 
		 * and save his current state to localStorage so that we can identify later on step 2
		 * @param  {Boolean}
		 */
		$scope.SignUpStepFirst = function (isValid, signupForm) {
			if( !isValid ){
				return;
			}
			$scope.isLoading = true;
			RestSvr.post('registration/', $scope.user)
			.then( response => $state.go('thankyou'))
			.catch( errors => {
				if( errors.data ){
					angular.forEach(errors.data, function (value, prop) {
						signupForm[prop].$setValidity('unique', false);
					});
				}
			})
			.then(() => $scope.isLoading = false);
		};

		/**
		 * This function will validate the signup step 2
		 * @param {isValid}
		 */
		$scope.SignUpStepTwo = function (isValid) {
			if( !isValid ){
				$scope.showHints = false;
				return;
			}
			signUpSrv.saveUserState($scope.user);
			$state.go('signup.three');
		};

		/**
		 * This function will validate the signup step 3
		 * @param {isValid}
		 */
		$scope.SignUpStepThree = function (isValid) {
			if( !isValid ){
				return;
			}
			signUpSrv.saveUserState($scope.user);
			$state.go('signup.four');
		};

		/**
		 * User can skip the step 3 using skip step function
		 * @return {}
		 */
		$scope.skipStepThree = function () {
			if( $scope.user ) {
				signUpSrv.saveUserState($scope.user);	
			}
			$state.go('signup.four');
		};

		/**
		 * System will verify the user password and confirm
		 * @param  {Boolean} isValid 
		 * @return {}
		 */
		$scope.signStepFour = function (isValid) {
			if( !isValid ) {
				return;
			}
			if( $scope.user.password !== $scope.user.confirm_password ){
				alertService.add('danger','Password and confirm should match');
				return;
			}
			signUpSrv.saveUserState($scope.user);
			$state.go('signup.five');
		};

		/**
		 * Most important step where we need to validate the customer's
		 * CQG details Username and Password
		 * @param  {Boolean} isValid [Check if Form is valid]
		 */
		$scope.signStepFive = function (isValid) {
			if( !isValid ){
				return;
			}
			if( $scope.user ) {
				signUpSrv.saveUserState($scope.user);	
			}
			$state.go('signup.six');
		};

		/**
		 * On step six of customer on-boarding user will
		 * validate through CQG using valid credentials
		 * @param  {Boolean} isValid Form or not
		 * @return {}          move to next step
		 */
		$scope.signStepSix = function (isValid) {
			if( !isValid ){
				return;
			}
			$scope.isLoading = true;
			websocketService.start("wss://demoapi.cqg.com:443", $scope.user,function (result) {
				
				var isLogonSuccess = (result.result_code == WebAPI.LogonResult.ResultCode.SUCCESS);
				if (Boolean(isLogonSuccess)) {
					$scope.$apply(function () { alertService.add('success', 'Authentication successful!! Rquesting Balance Report...' ); });
				    $log.info("baseTime: " + result.base_time);
				    var baseTime = new Date(Date.parse(result.base_time + "Z"));

				    $log.info("SUCCESSFUL logon Result:" + result.text_message);
				    $log.info("baseTime: " + baseTime);
				    $log.info("Session Token: " + result.session_token);
				    session_token = result.session_token;
				    //requestAccounts();
				    requestBalance();
				}
				else if( result.last_statement_balances_report ) {
					processLastStatementBalanceReport(result);
				}
				else {
				    $log.info("FAILED logon Result:" + result.text_message);
				    $scope.$apply(function () { 
				    	alertService.add('danger', result.text_message || 'Unable to Create connection, try again later!' );
				    	$scope.isLoading = false;
				    });
				    webSocketSvr.disconnect();
				}
		    });
		};


		/**
		 * this will clear the
		 * @param  input name attar
		 */
		$scope.clear = function (name, form) {
			form[name].$setValidity('unique', true);
		};

		/**
		 * Clear CQG Server side Errors
		 * @param { name } input attr name
		 */
		$scope.clearCQGError = function (name, form) {
			form[name].$setValidity('servererror', true);	
		};
		

		/// <summary> Process the balance information.</summary>
		/// <param name="id">The request id.</param>
		/// <param name="msg"> The second parameter.</param>
		function processLastStatementBalanceReport(obj){
		    $log.info('Balance info received: ' + obj.msg_id);
		    
		    alertService.add('success', 'Balance info received!! Connecting to server ...');
		    var brokerageMetadataData = obj.last_statement_balances_report;
		    var isSucceed = _.some(brokerageMetadataData.balance, hasSufficentFunds),
		    user = signUpSrv.getInfoByKey('user');
		    if( isSucceed ){
		    	var data = {
		    		session_token: session_token,
		    		brokerageMetadataData: brokerageMetadataData.balance,
		    		user: user,
		    		onboarding: isSucceed
		    	};
		    	successOnBoarding(data);
		    } else {
		    	failedOnBoarding(user);
		    }
		    localStorageService.remove('user');
		}

		/**
		 * function will post data if user sucessfully onboard
		 * @param  {Object} data [contains on-boarding info]
		 * @ {redirect user to confirmation }
		 */
		function successOnBoarding(data) {
		    RestSvr.post('update/user/details/', data)
			.then( response => $state.go('signup.confirm'))
			.catch(errors => alertService.add('danger', errors.message))
			.then(() => $scope.isLoading = false);
		}

		/**
		 * [failedOnBoarding will post data to server
		 * if customer has insufficent funds in his account
		 * @param  {Object} data {email: useremail}
		 * @ {redirect user to failed onboard}
		 */
		function failedOnBoarding(data) {
			RestSvr.post('failed/onboarding/', {email: data.email})
			.then(response => $state.go('signup.failed'))
			.catch(errors => alertService.add('danger', errors.message))
			.then(() => $scope.isLoading = false);
		}

		/**
		 * this function will check if customer account has
		 * sufficent funds to complete the onboarding process
		 * @param  {Array}  info array of balance
		 * @return {Boolean}      [if amount > $10k]
		 */
		function hasSufficentFunds(info) {
			return info.balance > 10000;
		}
	}
]);
