'use strict';

mimicTrade.factory('loginSrv', ['localStorageService', '$rootScope', function (localStorageService, $rootScope) {
    return {
        isLogged: false,
        initUserSession: function (user, token) {
            this.isLogged = true;
            localStorageService.set('token', token);
            localStorageService.set('authUser', user);
            $rootScope.user = localStorageService.get('authUser');
        },
        getToken: function () {
            return localStorageService.get('token');
        }
    };
}])
.factory('authSrv', ['loginSrv', 'localStorageService', '$rootScope', function (loginSrv, localStorageService, $rootScope) {
    return {
    	User: function () {
            if( localStorageService.get('authUser') ){
                return localStorageService.get('authUser');
            } else {
                $rootScope.clearToken();
            }
        }
    };
}]);
