'use strict';

mimicTrade.factory('notificationSrv', ['RestSvr', 'authSrv', (RestSvr, authSrv) => {
    return {
        getNotificationList: (page = 1) => RestSvr.paginate('notification/list/', {page, user_id: authSrv.User().id}),
        markAsReadNotifications: (notification_id) => RestSvr.paginate('notification/status/', {notification_id}),
        deleteNotifications: (notification_id) => RestSvr.paginate('notification/delete/', {notification_id}),
        getNotificationCount: () => RestSvr.post('notification/unread/count/', {user_id: authSrv.User().id})
    };
}]);