/* global  symbols_by_underlying_request, child_symbols_request */
'use strict';
mimicTrade.controller('traderStrategiesCtrl', ['$scope', '$state', 'RestSvr', 'traderSrv','authSrv','alertService','websocketService','$log','webSocketSvr','$timeout','$q','$rootScope','$location',
	($scope, $state, RestSvr, traderSrv, authSrv, alertService, websocketService, $log, webSocketSvr, $timeout, $q, $rootScope, $location) => {
		let symbolsByUnderlyingReport, baseTime, 
		strike = {}, // default value for strike array
		expDates = {}; // default value expiriation dates

		$scope.$on('$viewContentLoaded', function() {
			if($state.current.name == 'newStrategy') {
				// initialize core components
				require(["ProtoBuf"], function (ProtoBufInit) {
				    if ('WebSocket' in window) {
				        if ('ArrayBuffer' in window) {
				            initPage();
				        } else {
				            $log.info("ArrayBuffer is not supported");
				        }
				    } else {
				        $log.info("WebSockets are not supported");
				    }
				});
			}	
			$scope.fetchSubSectors();
			/**
			 * Reterieve Strike prices from CQG API,
			 * Once the view content loaded call the function
			 */
			$timeout(() => strike_price());
		});

		/**
		 * Check if user strategy has already stored in localStorage
		 * @type {Object}
		 */
		let strategyData = traderSrv.getLocallySavedStrategy();
		
		/**
		 * Strategy Object which will hold the info of strategy	
		 * @type {Object}
		 */
		$scope.strategy = {};
		
		if( strategyData ){
			$scope.strategy = strategyData;
			if( $scope.strategy.product_category ){
				$timeout(() => $scope.fetchContracts($scope.strategy.product_category, true));
			}
			if( $scope.strategy.instrument ){
				$timeout(() => $scope.fetchSpreads($scope.strategy.instrument, true));
			}
		}

		let user = authSrv.User();

		/**
		 * Current date for every new strategy
		 * @type {Date Object}
		 */
		$scope.today = new Date();

		/**
		 * Array of months between start date to end date
		 * @type {Array}
		 */
		$scope.monthsArr = getDates($scope.today, moment($scope.today).add(1, 'months'));

		/**
		 * Strike price range between two constant of prices
		 * @type {Array}
		 */
		$scope.strikePriceRange = _.range(1100, 2020, 20);

		
		/**
		 * Number of legs that contained by a Strategy
		 * @type {Array}
		 */
		if( !strategyData ){
			$scope.strategy.legs = [];
		}

		
		/**
		 * Number of legs straddle has
		 * @type {Array}
		 */
		$scope.straddle_legs = [1,2,4,5];
		
		/**
		 * check if form is valid only then submit the form
		 * @param  {Boolean} isValid [description]
		 * @return {[type]}          [description]
		 */
		
		$scope.createNewStrategy = function (isValid) {
			if( !isValid ){
				return;
			}
			$scope.isLoading = true;
			_.assign($scope.strategy, {user_id: user.id});
			traderSrv.saveStrategyToLocal($scope.strategy);
			$scope.isLoading = false;
			$state.go('reviewStrategy');
		};


		/**
		 * return an array of dates between start abd end date
		 * @param  {Date} startDate 
		 * @param  {Date} stopDate
		 * @return {Array}           [array that contains dates]
		 */
		function getDates(startDate, stopDate) {
		    var dateArray = [],
		    currentDate = moment(startDate);
		    stopDate = moment(stopDate);
		    while (currentDate <= stopDate) {
		        dateArray.push( moment(currentDate).format('MMM DD') );
		        currentDate = moment(currentDate).add(1, 'days');
		    }
		    return dateArray;
		}

		/**
		 * Intially fetch all the sub-sectors for traders
		 * @param  {integer}  							[trader id]
		 * @return {Object}                            [sub sectors]
		 */
		$scope.fetchSubSectors = () => traderSrv.getSubSectors(user.id).then(({records}) => $scope.sub_sectors = records).catch(err => alertService.add('danger', err.message));
		

		/**
		 * Fetch contracts on behalf of sub-sectors 
		 * assigned to particular trader
		 * @param  {integer} sub_sector_id [sub sector id]
		 * @return {Object}               [List of contracts]
		 */
		$scope.fetchContracts = (sub_sector, whenCall) => {
			/**
			 * We must check the key before removing to make sure if it exists or not
			 * @param  {String} $scope.strategy.hasOwnProperty('instrument') [Key]
			 * @return {Boolean}                                                [true/false]
			 */
			if( $scope.strategy.hasOwnProperty('instrument') && !whenCall){
				delete $scope.strategy.instrument;
			}
			return traderSrv.getContracts(sub_sector.sub_sector_id).then(({records}) => $scope.instruments = records).catch(err => alertService.add('danger', err.message));
		};
		

		/**
		 * Fetch spreads on behalf of contracts
		 * assigned to particular trader
		 * @param  {integer} sub_sector_id [sub sector id]
		 * @return {Object}               [List of spreads]
		 */
		$scope.fetchSpreads = (instrument, whenCall) => {
			/**
			 * We must check the key before removing to make sure if it exists or not
			 * @param  {String} $scope.strategy.hasOwnProperty('spread_definitions') [Key]
			 * @return {Boolean}                                                [true/false]
			 */
			if( $scope.strategy.hasOwnProperty('spread_definitions') && !whenCall){
				delete $scope.strategy.spread_definitions;
			}
			traderSrv.getSpreads(instrument.id).then(({records}) => $scope.spread_definitions = records).catch(err => alertService.add('danger', err.message));
		};
        
        
        function strike_price(){
            let cqg_data = {
               cqg_id: 'MIMICWEBAPI', 
               cqg_password: 'pass'
            };
            /*
            Below process to have all the strikes and other useful information: 
			1. Send symbol resolution request (inside information request) on CLE
			2. Receive CLE’s contract_id (inside information report).
			3. Send symbols_by_underlying_request (inside information request) with underlying_contract_id = contract_id (in step 2)
			4. Receive multiple “id” (inside symbol inside symbols_by_underlying_report) like “123456” (it is string type although it looks like an int)
			5. Send child_symbols_request (inside information request) with parent_symbol_id = “id” (in step 4)
			6. Receive multiple “symbol” messages (inside child_symbols_report) with “id” and “strike” and other details.
			7. It should be easy to go through the symbol message (in step 6) and collect all strikes you want.
             */
			websocketService.start("wss://demoapi.cqg.com:443", cqg_data,function (result) {

				var isLogonSuccess = result.result_code == WebAPI.LogonResult.ResultCode.SUCCESS;
				if (Boolean(isLogonSuccess)) {
					$scope.$apply(function () { alertService.add('success', 'Authentication successful!! Connected' ); });
				    baseTime = Date.parse(result.base_time);
				    let rawBaseTime = new Date(Date.parse(result.base_time + "Z"));

				    $log.info("SUCCESSFUL logon Result:" + result.text_message);
				    $log.info("rawBaseTime: " + rawBaseTime);
				    
				    $log.info("Session Token: " + result.session_token);
				    let session_token = result.session_token;
				} else if( result.symbol_resolution_report ){
					processSymbolResolutionReport(result.symbol_resolution_report);
				} else if( result.symbols_by_underlying_report ){
					processSymbolsByUnderlyingReport(result.symbols_by_underlying_report);
				} else if( result.child_symbols_report ){
					processChildSymbolsReport(result.child_symbols_report);
				}
				else {
				    $log.info("FAILED logon Result::" + result.text_message);
				    $rootScope.$apply(function () { 
				    	alertService.add('danger', result.text_message || 'Connection cloesd by remote host, will reconnect in a moment, otherwise try to refresh page once' );
				    });
				    webSocketSvr.disconnect();
				}
		    });
        }
        
        /**
         * Default options values for legs options
         * @type {Array}
         */
        $scope.legsOpt = [['BUY','SELL'],['CALL','PUT']];
        
        /**
         * Fire a symbol resolution report when shortcut button is clicked
         * @return {String} [Symbol Name]
         */
        $scope.symbol_resolution_report = (symbol_name) => {
        	$scope.processSymbol = true;
        	strike = {}; // Reset default value for strike array
			expDates = {}; // Reset default value expiriation dates
        	webSocketSvr.symbol_resolution_request(symbol_name);
        };

        /**
         * [processSymbolResolutionReport description]
         * @param  {String} result [Symbol Name]
         * @return {Object}
         */
        function processSymbolResolutionReport(result) {
        	if( result.contract_metadata ){
        		symbols_by_underlying_request(result.contract_metadata.contract_id);	
        	}
        }

        /**
         * [processSymbolsByUnderlyingReport description]
         * @param  {Array} result
         * @return {Object}
         */
        function processSymbolsByUnderlyingReport(result) {
        	if( result.symbol ){
        		symbolsByUnderlyingReport = result.symbol;
        		process_symbol_by_uderlying_report();
        	}
        }

        /**
         * Remove strategy legs from the legs array on the basis of index
         * @param  {int} index [description]
         * @return {}       
         */
        $scope.removeSelected = (index) => {
        	if( $scope.strategy.legs ){
        		$scope.strategy.legs.splice(index, 1);
        	}
        };

        function process_symbol_by_uderlying_report() {
        	// Popout last item from array
        	if( symbolsByUnderlyingReport.length > 0 ) {
        		let lastItem = symbolsByUnderlyingReport.pop();
        		processChildSymbolsRequest(lastItem);	
        	} else {
        		$scope.strike = strike;
        		$scope.expDates = expDates;
        		$scope.$apply(() => $scope.processSymbol = false);
        	}
        }

        function processChildSymbolsRequest(item) {
        	if(!item){
        		return;
        	}
        	child_symbols_request(item.id);
        }

        
        function processChildSymbolsReport(data) {
        	if( data ){
        		let _symbol = data.symbol,
        		symbolLength = _symbol.length;
        		
        		if( symbolLength > 0 ){
        			
        			let i = 0;
        			for(; i< symbolLength; i++){
        				if(!angular.isUndefined(_symbol[i].contract_metadata)){
        					strike[_symbol[i].contract_metadata.strike] = _symbol[i].contract_metadata.strike;
        					let exp_timestamp = baseTime + _symbol[i].contract_metadata.last_trading_date.low,
        					formatted_date = moment(exp_timestamp).format('MMM DD');
        					expDates[formatted_date] = formatted_date;
        				}
        			}
        		}
        	}
        	$timeout(() => process_symbol_by_uderlying_report(), 1000);
        }

        $rootScope.$on('websocket disconnected', function (event, data) {
        	alertService.add('success', 'Reconnecting .... !!');
        	$timeout(() => strike_price(), 1000);
        });
	}
]);
