'use strict';

/* Application routes */
mimicTrade.config(['$stateProvider', '$urlRouterProvider', '$locationProvider', 
	function($stateProvider, $urlRouterProvider, $locationProvider){
		// the known route
    	$urlRouterProvider.when('', '/');

    	// For any unmatched url, send to 404
		//$urlRouterProvider.otherwise('/404');
		$urlRouterProvider.otherwise(function($injector, $location){
		   var state = $injector.get('$state');
		   state.go('404');
		   return $location.path();
		});
		$stateProvider
		.state('home',{
			url: '/',
			controller: 'homeCtrl',
			templateUrl: '/user/home/views/home.html',
			authenticate: false,
			data: {pageTitle: 'Home'}
		})
		.state('404',{
			templateUrl: '/user/partials/404.html',
			data: {pageTitle: '404 Page Not Found'},
			authenticate: false,
			resolve: {
			    deps: ['$ocLazyLoad', function($ocLazyLoad) {
			        return $ocLazyLoad.load({
			            name: 'mimicTrade',
			            insertBefore: '#ng_load_plugins_before',
			            files: [
			                '/assets/pages/css/error.min.css',
			            ] 
			        });
			    }]
			},
		})
		.state('500',{
			templateUrl: '/user/partials/500.html',
			data: {pageTitle: 'Internal Server Error'},
			authenticate: false,
			params: {
		        obj: null
		    },
			resolve: {
			    deps: ['$ocLazyLoad', function($ocLazyLoad) {
			        return $ocLazyLoad.load({
			            name: 'mimicTrade',
			            insertBefore: '#ng_load_plugins_before',
			            files: [
			                '/assets/pages/css/error.min.css',
			            ] 
			        });
			    }]
			},
		});
		//$locationProvider.html5Mode(true);
	}
]);
