'use strict';

/* Application routes */
mimicTrade.config(['$stateProvider', function($stateProvider){
	$stateProvider
	.state('strategies',{
		url: '/traders/strategies',
		controller: 'traderCtrl',
		templateUrl: '/user/traders/views/trader_strategies_list.html',
		authenticate: true,
		data: {pageTitle: 'Strategies Listing'},
		resolve: {
			strategiesList: ['traderSrv', (traderSrv) => traderSrv.getMyStrategies()]
		}
	})
	.state('newStrategy',{
		url: '/traders/create-strategy',
		controller: 'traderStrategiesCtrl',
		templateUrl: '/user/traders/views/trader_create_strategy.html',
		authenticate: true,
		data: {pageTitle: 'Create New Strategy'},
		resolve: {
		    deps: ['$ocLazyLoad', function($ocLazyLoad) {
		        return $ocLazyLoad.load({
		            name: 'mimicTrade',
		            insertBefore: '#ng_load_plugins_before',
		            files: [
		                '/CQGWebAPIJavaScriptClient/scripts/realTime.js',
		                '/CQGWebAPIJavaScriptClient/scripts/account.js',
		                '/CQGWebAPIJavaScriptClient/scripts/auxiliary.js',
		                '/CQGWebAPIJavaScriptClient/scripts/sessionInfo.js',
		                '/CQGWebAPIJavaScriptClient/scripts/timeAndSales.js',
		                '/CQGWebAPIJavaScriptClient/scripts/timeBars.js',
		                '/CQGWebAPIJavaScriptClient/scripts/webapi.js',
		                '/CQGWebAPIJavaScriptClient/scripts/ui.js'
		            ] 
		        });
		    }]
		},
	})
	.state('reviewStrategy',{
		url: '/traders/review-startegy',
		controller: 'reviewStrategyCtrl',
		templateUrl: '/user/traders/views/review_strategy.html',
		authenticate: true,
		data: {pageTitle: 'Review Strategies'}
	})
	.state('viewStrategy',{
		url: '/traders/view-strategy/:id',
		controller: 'viewStrategyCtrl',
		templateUrl: '/user/traders/views/trader_view_strategy.html',
		authenticate: true,
		data: {pageTitle: 'View Strategy'},
		resolve: {
			strategy: ['traderSrv', '$stateParams', (traderSrv, $stateParams) => traderSrv.getStrategyById($stateParams.id)]
		}
	});
}]);	
