'use strict';

/* Application config */
/*Angular interceptors are service factories that are registered with the $httpProvider */
mimicTrade.config(['$httpProvider',($httpProvider) => {
	let interceptor = ['$q', '$rootScope', 'loginSrv', ($q, $rootScope, loginSrv) => {
        return {
        	request: (config) => {
	           	config.headers = config.headers || {};
                let token = loginSrv.getToken();
                
                if (token) {
                    config.headers.Authorization = `Token ${token}`;
                    loginSrv.isLogged = true;
                }
	           	return config;
	       	},

            requestError: (rejection) => {
                return $q.reject(rejection);
            },

            response: (response) => {
                return response || $q.when(response);
            },

            // Revoke client authentication if 400 is received
            responseError: (rejection) => {
                if( 'onLine' in navigator ){
                    if( !navigator.onLine ){
                        $rootScope.$broadcast('server_error', {message:'ERR_INTERNET_DISCONNECTED'});
                        return;    
                    }
                }
                
                if(rejection.status <= 0) {
                    $rootScope.$broadcast('server_error', {message:'ERR_CONNECTION_REFUSED'});
                    return;
                }
                
                if(rejection.status === 401) {
                    if(rejection.data.detail === 'Invalid token.') {
                        $rootScope.$broadcast('server_error',{message:'Your Session has been expired, please login again to continue.', status_code: 401});
                        return;
                    }
                }
                
                return $q.reject(rejection);
            }
        };
    }];

	$httpProvider.interceptors.push(interceptor);
}])
.config(['$compileProvider', ($compileProvider) => {
    $compileProvider.debugInfoEnabled(false);
    $compileProvider.commentDirectivesEnabled(false);
    $compileProvider.cssClassDirectivesEnabled(false);
}])
.config(['$mdThemingProvider',($mdThemingProvider) => {
    /**
     * Configuration for angular material custom theme .
     *
     * The goal of this file is to allow the custom theme setup
     * for more infomation visit
     * https://material.io/guidelines/style/color.html#color-color-palette
     */ 
    $mdThemingProvider.definePalette('primary', {
        '50':  '21A37B',
        '100': '21A37B',
        '200': '21A37B',
        '300': '21A37B',
        '400': '21A37B',
        '500': '21A37B', //on bg color raised
        '600': '21A37B', //on hover raised button
        '700': '21A37B',
        '800': '21A37B',
        '900': '21A37B',

        'A100': '14825D', //warn
        'A200': '14825D',//warn
        'A400': '14825D',//accent on mouse hover
        'A700': '21A37B',//accent on mouse hover
        'contrastDefaultColor': 'light',    // whether, by default, text (contrast)
                                            // on this palette should be dark or light

        'contrastDarkColors': ['50', '100', //hues which contrast should be 'dark' by default
         '200', '300', '400', 'A100'],
        'contrastLightColors': undefined    // could also specify this if default was 'dark'
      });

      $mdThemingProvider.theme('default')
        .accentPalette('primary')
        .primaryPalette('primary');

        //md-raised for button
    
    // $mdThemingProvider.theme('default')
    //     .primaryPalette('green')
    //     .accentPalette('green');

    $mdThemingProvider.enableBrowserColor({
        theme: 'default', // Default is 'default'
        palette: 'primary', // Default is 'primary', any basic material palette and extended palettes are available
        hue: '800' // Default is '800'
    });
}])
.run(['$location','$rootScope','loginSrv','$state','localStorageService','$timeout','$mdSidenav','alertService','$injector','notificationSrv',
($location, $rootScope, loginSrv, $state,localStorageService, $timeout, $mdSidenav, alertService, $injector, notificationSrv) => {

		$rootScope.$on("$stateChangeStart", (event, toState, toParams, fromState, fromParams) => {
            alertService.closeAlert();
            if (toState.authenticate && !loginSrv.isLogged && !localStorageService.get('authUser')){
                loginSrv.isLogged = false;
                $state.go("login");
                event.preventDefault(); 
            }else {
                let token = localStorageService.get('token');
                if( token && !toState.authenticate){
                    let userData = localStorageService.get('authUser');
                    if(userData.user_type === 'Trader') {
                        $state.go('strategies');        
                    } else {
                        $state.go('customer');      
                    }
                }
            }
            if( toState.name === "signup.six" || toState.name === "newStrategy"){
            	loadMyCQGDemoApp();
            }
		});

        $rootScope.$on('$stateChangeError', (event, toState, toParams, fromState, fromParams, error) => {
            if( error.errors ) {
                
                /**
                 * This will help in resolve 500 state
                 * without url change
                 */
                let state = $injector.get('$state');
                state.go('500');
                alertService.add('danger', error.message);
                return $location.path();
            }
        });

        /* This will logout the user from the application */
        $rootScope.clearToken = () => {
            localStorageService.remove('token');
            localStorageService.remove('authUser');
            delete $rootScope.user;
            loginSrv.isLogged = false;
            $state.go("login");
        };

        /**
         * Open sidebar
         * @param  {String} navId is #id of sidebar
         */
        $rootScope.toggleSideNav = (navId) => {
        	$mdSidenav(navId).toggle();
      	};

        // Set the user for entire application
        $rootScope.user = localStorageService.get('authUser');
        
        $rootScope.$state = $state; // state to be accessed from view

        // If any global error occured
        $rootScope.$on('server_error', (event, res) => {
            
            if(res.status_code){
                if(res.status_code === 401){
                    $rootScope.clearToken();
                }
            }
            alertService.add('danger', res.message);
        });

        $rootScope.closeAlert = (index) => {
            alertService.closeAlert();
            delete $rootScope.cqg_error;
        };

        /**
         * manage count will manage the count of user's notificaions
         * @param  {Object} data [notification data object]
         * @return {}
         */
        $rootScope.manageCount = (data) => {
            if( data ) {
                $rootScope.notificationCount = data.records.count;    
            } else {
                if( $rootScope.notificationCount > 0 ) {
                    $rootScope.notificationCount -= 1;
                }
            }
        };

        /**
         * this function will redirect user to notifications url
         * @param  {Object} data [notifications data object]
         * @return {}
         */
        $rootScope.goToNotificationUrl = (data) => {
            notificationSrv.markAsReadNotifications([data.id])
            .then(response => {
                $rootScope.manageCount();
                $location.path(data.url);
            })
            .catch(errors => alertService.add('danger', errors.message));
        };
    }
]);

// Just for demo purpose delete later
let loadMyCQGDemoApp = () => {
	var demoScript = document.getElementById('requireDemoApp');
	demoScript.setAttribute('data-main', '/CQGWebAPIJavaScriptClient/scripts/main');
	demoScript.setAttribute('src', '/CQGWebAPIJavaScriptClient/scripts/require.js');
};
