'use strict';

/* Application routes */
mimicTrade.config(['$stateProvider', function($stateProvider){

	let strategy = ['customerSrv', '$stateParams', (customerSrv, $stateParams) => customerSrv.getStrategyById($stateParams.id)];

	$stateProvider
	.state('customerStrategies',{
		url: '/customers/strategies',
		controller: 'customerCtrl',
		templateUrl: '/user/customers/views/customer_strategies_list.html',
		authenticate: true,
		data: {pageTitle: 'Strategies Listing'},
		resolve: {
			strategies: ['customerSrv', (customerSrv) => customerSrv.getAllTradersStrategies()]
		}
	})
	.state('customerViewStrategy',{
		url: '/customers/view-strategy/:id?action',
		controller: 'customerViewStrategyCtrl',
		templateUrl: '/user/customers/views/customer_view_strategy.html',
		authenticate: true,
		data: {pageTitle: 'View Strategy'},
		resolve: {
			strategy
		}
	})
	.state('closeStrategy',{
		url: '/customers/close-strategy/:id?action',
		controller: 'closeStrategyCtrl',
		templateUrl: '/user/customers/views/close_strategy.html',
		authenticate: true,
		data: {pageTitle: 'Close Strategy'},
		resolve: {
			strategy
		}
	});
}]);	
