'use strict';
mimicTrade.controller('viewStrategyCtrl', ['$scope','traderSrv', 'alertService','$state','strategy','dialogService','$log',
	($scope, traderSrv, alertService, $state, strategy, dialogService, $log) => {
		/**
		 * Get strategy detail by startegy id
		 * @type {Object}
		 */
		$scope.strategy = strategy.records;
		$scope.currentDate = new Date(); // Current/Today date
		$scope.comments = [];
		$scope.commentLoader = false;
		/**
		 * This function will executes when view loaded and 
		 * then strategy comments will loaded
		 */
		let load = (page, scroll) => 
			traderSrv.getStrategyComments($scope.strategy.id, page)
			.then(response => { 
				/* global scrollToBottom */
				$scope.comments = $scope.comments.concat(response.records); 
				$scope.paging = response.paging; 
				$scope.commentLoader = true;
				if(scroll){
					scrollToBottom('commentList');	
				}
				
			})
			.catch(errors => alertService.add('danger', errors.message));
		

		load();

		/**
		 * Update page title according to strategy title
		 * @type {Object}
		 */
		$state.current = {
			data: { pageTitle: $scope.strategy.title }
		};

		
		/**
		 * Update function will add new comments for strategy
		 * @param  {string} [comment text]
		 */
		$scope.update = (comment) => {
			if(!comment){
				alertService.add('danger', 'Please add comment!');
				return;
			}
			traderSrv.saveStrategyComments(comment, $scope.strategy.id)
			.then(response => $scope.comments.unshift(response.records))
			.catch(errors => alertService.add('danger', errors.message))
			.then(() => $scope.comment = '');
		};

		/**
		 * Remove strategy will archived the strategy in Db
		 * @param  {Object} event [click event handler]
		 * @return {Object}
		 */
		$scope.removeStrategy = (event) => {
			traderSrv.archivedStrategy(event, $scope.strategy.id)
			.then(response => {
				$state.go('strategies');
				alertService.add('success', 'Strategy has been archived successfully');
			})
			.catch(errors => $log.info(errors));
		};

		/**
		 * Load more comments
		 * @return {Object}
		 */
		$scope.loadMore = () => {
			$scope.commentLoader = false;
			load(($scope.paging.page + 1), true);
		};
	}
]);	