'use strict';

/* Application routes */
mimicTrade.config(['$stateProvider', '$urlRouterProvider',function($stateProvider, $urlRouterProvider){

	let positions = ['positionSrv', (positionSrv) => positionSrv.getAllPositions()];

	$stateProvider
	.state('customer',{
		url: '/positions/list',
		controller: 'positionCtrl',
		templateUrl: '/user/positions/views/list.html',
		authenticate: true,
		data: {pageTitle: 'Positions'},
		resolve: {
			positions
		}
	})
	.state('customer.grid',{
		url: '^/positions/grid',
		templateUrl: '/user/positions/views/grid.html',
		authenticate: true,
		data: {pageTitle: 'Positions'},
		resolve: {
			positions
		}
	});
}]);