'use strict';
mimicTrade.factory('RestSvr', ['$http', '$window', '$q',
	($http, $window, $q) => {
		return{
			login: (apiUrl, data) => {
				let req = {
					method: 'POST',
					url: baseUrl(apiUrl),
				 	data: data
				};
				return $q((resolve, reject) => {
					$http(req).then(response => {
						resolve({
							user: response.data.user,
							token: response.data.token
						});
					})
					.catch(errors => {
						reject({
							errors: true,
							status: errors.status,
							message: errors.data.message || errors.data.detail || 'Internal Server Error'
						});
					});
				});
			},
			post: (apiUrl, data) => {
				let req = {
					method: 'POST',
					url: baseUrl(apiUrl),
				 	data: data
				};
				return $q((resolve, reject) => {
					$http(req).then(response => {
						resolve({
							records: response.data.result
						});
					}).catch(errors => {
						reject({
							errors: true,
							data: errors.data,
							message: errors.data.message || errors.data.detail || 'Internal Server Error'
						});
					});
				});	
			},
			paginate: (apiUrl, data, params) => {
				let req = {
					method: 'POST',
					url: baseUrl(apiUrl),
				 	data: data,
				 	params: params
				};
				return $q((resolve, reject) => {
					$http(req).then(response => {
						resolve({
							records: response.data.result,
							paging: response.data.paging
						});
					})
					.catch(errors => {
						reject({
							errors: true,
							data: errors.data,
							message: errors.data.message || errors.data.detail || 'Internal Server Error'
						});
					});
				});	
			},
			get: (apiUrl, params) =>{
				let p = !angular.isUndefined(params) ? params : null;
				return $q((resolve, reject) => {
					$http.get(baseUrl(apiUrl), p).then(response => {
						resolve({
							records: response.data.result
						});
					}).catch(errors => {
						reject({
							errors: true,
							data: errors.data,
							message: errors.data.message || errors.data.detail || 'Internal Server Error'
						});
					});
				});	
			}
		};
	}
]);

function baseUrl (apiUrl) {
	let protocol = window.location.protocol,
	baseUrl = (['localhost', 'local.mimic.com'].indexOf(window.location.hostname) > -1 ) ? protocol + '//' + '100.100.7.76:8010' : protocol + '//' + '130.211.224.61:8010';
	return 	baseUrl + '/api/' + apiUrl;
}
