    /**
    * [description]
    * @param  {selctor} id [html element id]
    * @return {[type]}    [description]
    */
    function scrollToTop (id) {
        let offset;
        if(id){
            offset=$('#'+id).prop('offsetTop');
        }

        $('html,body').animate({
            scrollTop: offset || 0
        },500);
    }

    function scrollToBottom(className) {
        $(`.${className}`).animate({scrollTop: $(`.${className}`).prop('scrollHeight')}, 'slow'); 
    }
