'use strict';
mimicTrade.controller('homeCtrl', ['$scope', '$state', 'RestSvr', '$rootScope', '$location','$mdDialog', 'localStorageService',
	function($scope, $state, RestSvr, $rootScope, $location, $mdDialog, localStorageService){
		/**
		 * Confirmation dialog box will apears on-behalf of query string 
		 * if user verify his link or not
		 * @type {[type]}
		 */
		var queryString = $location.search();
		if( queryString ){
			if( queryString.email_verify && queryString.uid){
				$mdDialog.show({
					controller: DialogController,
					templateUrl: './user/partials/email_verification_dialog.html',
					parent: angular.element(document.body),
					clickOutsideToClose:true
				})
				.then(function(answer) {
					localStorageService.set('user', {id: queryString.uid});
					$state.go('signup.agreement');
				});
			}
		}

		/**
		 * Custom controller needs dependency injection 
		 * @type {array of dependencies}
		 */
		DialogController.$inject = ['$scope','$mdDialog'];
		function DialogController($scope, $mdDialog) {
		    $scope.answer = function(answer) {
		    	$mdDialog.hide(answer);
		    };
		}
	}
]);