'use strict';
var websocket;
mimicTrade.constant("WEBSOCKET", {
    "URL": "wss://demoapi.cqg.com:443"
})
.factory('websocketService', ['webSocketSvr','$log','$rootScope','alertService',
    function (webSocketSvr, $log, $rootScope, alertService) {
        return {
            start: function (url, data,callback) {
                // if (websocket != null) {
                //     $log.info("Already connected.");
                //     return;
                // }

                try{
                    websocket = new WebSocket(url);
                    websocket.binaryType = 'arraybuffer'; 
                    websocket.onopen = function () {
                        var logon = {};
                        var clMsg = new WebAPI.ClientMsg;
                        logon.user_name = data.cqg_id;
                        logon.password = data.cqg_password;
                        logon.client_app_id = "WebApiTest";
                        logon.client_version = "0.1";
                        logon.collapsing_level = 0;
                        clMsg.logon = logon;
                        sendMessage(clMsg, "Sent: Login: " + data.cqg_id + " : " + data.cqg_password);
                    };
                    websocket.onclose = function () {
                        $log.info("Disconnected - status " + this.readyState);
                        websocket = null;
                        $rootScope.$broadcast('websocket disconnected', {status: this.readyState});
                        callback("Disconnected - status " + this.readyState);
                    };
                    websocket.onmessage = function (msg) {
                        var result = processMessage(msg.data);
                        webSocketSvr.processResult(result, callback);
                    };
                    websocket.onerror = function (evt) {
                        webSocketSvr.disconnect();
                        callback(evt);  
                    };
                }catch(e){
                    console.log(e);
                    callback(e);
                }
            }
        };
    }
])
.factory('webSocketSvr', ['$log', 'WEBSOCKET', '$rootScope','$q', function($log, WEBSOCKET, $rootScope, $q) {
    
    var webSocketSvr = {};
    
    webSocketSvr.processResult = function (result, callback) {
        if(angular.isUndefined(result) ){
            return;
        }

        if (result.logon_result)
            callback(result.logon_result);

        for (var i = 0; i < result.information_report.length; ++i)
            callback(processInformationReport(result.information_report[i]));
    };

    /**
     * Send a request for custom symbol resolution
     * @param  {String} symbol [eg: 'EP']
     * @return {Object}        [contract_metadata]
     */
    webSocketSvr.symbol_resolution_request = function (symbol) {
        console.log(websocket);
        return $q((resolve, reject) => {
            /* global resolveSymbol */
            resolveSymbol(symbol, (resolution) => {
                if( resolution ) {
                    resolve(resolution);
                } else {
                    reject();
                }
            });
        });
    };

    webSocketSvr.disconnect = function () {
        if (!websocket) {
            $log.info("Not connected.");
        } else {
            websocket.close();
            websocket = null;
        }
    };

    return webSocketSvr;
}]);
