'use strict';

mimicTrade.directive('notificationPanel', ['notificationSrv', '$location','$rootScope', 
	function(notificationSrv, $location, $rootScope) {
		return {
			restrict: 'A',
			link: function (scope, elem) {
				// For notifications count
				notificationSrv.getNotificationCount()
				.then($rootScope.manageCount)
				.catch(errors => console.log(errors));

				// for notification list
				elem.on('click', () => {
					notificationSrv.getNotificationList()
					.then(({records, paging}) => {
						scope.notifications = records;
					})
					.catch(errors => console.log(errors));
				});
			},
			templateUrl: '/user/notifications/directives/notification-panel.html'
		};
	}
]);	