'use strict';

mimicTrade.directive('pageLoader', [ () => {
	return {
		restrict: 'E',
		scope: {
			loading: '=', // string
			style: '@?' // string , ? makes it optional
		},
		transclude: true,
		template: `<div ng-hide="loading" layout="column" layout-align="center center" style="{{style}}">
						<md-progress-circular md-mode="indeterminate"></md-progress-circular>
						<p><ng-transclude>Please wait while we are loading ....</ng-transclude></p>
    				</div>`,
		link: (scope) => {
			if( _.isEmpty(scope.style) ){
				scope.style = 'min-height: 350px;';
			}
		}
	};
}]);